@keyframes shoot {
  0% {
    opacity: 1;
    transform: translate(500px, -500px);
  }
  40% {
    opacity: .6;
  }
  100% {
    opacity: 0;
    transform: translate(-500px, 500px);
  }
}

@keyframes animatedSeparatorHorizontal {
  0% {
    opacity: 0;
    transform: translateX(200%);
  }
  10% {
    opacity: 1;
  }
  50% {
    opacity: 0.2;
    transform: translateX(-100%);
  }
  60% {
    opacity: 0.6;
  }
  70% {
    opacity: 0;
  }
  100% {
    transform: translateX(-400%);
  }
}

@keyframes foldScrollDownPointer {
  0% {
    opacity: 0;
    transform: translateY(0);
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(20px);
  }
}

