// =================
// New Home Settings
// =================
clearfix{
  &:before,&:after{
    content: "";
    display: block;
    clear: both;
  }
}

.section-title{
  border-bottom: 1px solid $border-color;
  padding-bottom: 0px;
  @extend clearfix;
  .text{
    position: relative;
    font-size: 24px;
    color: $color-text;
    margin: 0;
    float: right;
    line-height: 38px;
    &:before {
      content: "";
      position: absolute;
      right: 0;
      width: 100%;
      border-bottom: 1px solid $color-text;
      height: 2px;
      bottom: -1px;
    }    
  }
  .more-btn{
    float: left;
    min-width: 30px;
    span{
      font-size: 12px;
      line-height: 38px;
    }
    .material-icons{
      width: 100%;
      text-align: center;
    }
  }
}

// ===============
// New Main Card
// ===============
.fr_banner{
  .banner_wrapper{
    min-height: 136px;
    background-color: #e78406;
    background: radial-gradient( circle, #fcb320 0%, #e27900 100%);
    border-radius: 15px;
    position: relative;
    padding-left: 150px;
    padding-top: 15px;
    &:before{
      content: "";
      position:  absolute;
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      z-index: 4;
      background: url("/frontend/images/starfield.png");
      background-position: center;
      background-size: cover;    
    }
    &:after{
      content: "";
      position:  absolute;
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      z-index: 3;
      background: url("/frontend/images/fri-img-1.png");
      background-position: right center;    
      background-repeat: no-repeat;
      border-radius: 15px;
    }
    .logo-img{
      position: absolute;
      width: 80px;
      height: 70px;
      background-image: url('/frontend/images/logo-light.png');
      opacity: 0.3;
      left: 15px;
      top: 0;
      transform: translate(0, 50%);
      z-index: 10;
      background-size: 95%;
      background-repeat: no-repeat;
      background-position: center;
    }
    .banner_content{
      position: relative;
      z-index: 12;
      @include clearfix;
      .title{
        color: #fff;
        font-size: 50px;
        font-weight: 700;
        display: inline-block;
        float: left;
        margin-left: 30px;
        padding-bottom: 12px;
        span{
          position: relative;
          display: inline-block;
          color: #000;
          z-index: 4;
          line-height: 51px;
          padding: 0 1px 0 4px;
          &:before{
            content: "";
            position: absolute;
            width: 100%;
            height: 60px;
            top: 0;
            right: 0;
            z-index: -1;
            background-color: #fff;
            border-radius: 4px;
          }       
        }
      }
      #countholder {
        float: left;
        color: #fff;
        display: inline-block;
        text-align: center;
        font-size: 34px;
        border-radius: 6px;
        line-height: 1.4;
        font-family: "JF Flat", sans-serif;
        &> div{
          padding: 4px 10px;
          border-radius: 3px;
          margin-bottom: 12px;
          display: inline-block;
        }
        .banner_content_hint {
            display: block !important;
            text-align: center;
          }        
        div > span{
        border-radius: 3px;
        min-width: 60px;
        display: inline-block;
        }
        .smalltext{
        padding-top: 5px;
        font-size: 16px;
        }
        .spacer{
          line-height: 40px!important;
          display: inline-block;
          transform: translateY(-100%);
        }
      }
    }
    @media (max-width: 998px) {
      &:after{
        opacity: 0.6;
      }
    }
    @media (max-width: 685px) {
      padding-left: 0;
      padding-bottom: 60px;
      &:after{
        opacity: 0.2;
      }
      .banner_content{
        #countholder {
          float: none;
          display: block; 
          font-size: 30px;
          text-shadow: 0px 1px 2px #666;
          div > span{
            min-width: 40px;
          }
        }
        .title{
          color: #fff;
          font-size: 30px;
          font-weight: 600;
          display: block;
          float: none;
          text-align: center;
          margin: 15px 0;
          span{
            line-height: 38px;
            &:before{
              height: 40px;
            }       
          }
        }      
      }
      .logo-img{
        position: absolute;
        width: 70px;
        height: 50px;
        background-image: url('/frontend/images/logo-light.png');
        opacity: 0.8;
        left: 50%;
        top: auto;
        bottom: 15px;
        background-size: contain;
        transform: translate(-50%, 0);
      }      
    }
  }
}
// ===============
// New Main Card
// ===============
.card-wrapper{
  min-width: 120px;
  background-color: #FFF;
  border-radius: 10px;
  margin-top: 30px;
  .card-img{
    position: relative;
    padding: 10px;
    overflow: hidden;
    min-height: 100px;
    max-height: 210px;
    img{
      border-radius: 10px;
      display: block;
      max-width: 100%;
      height: 150px;
      margin: auto;
    }
  }
  .card-meta{
    padding: 0 10px 10px !important;
    .btn-link{
      font-family: $font-jfflat;
      text-align: right;
      color: lighten( $color-text , 20%);
      height: 30px;
      overflow: hidden;
      white-space: normal;
      &:before{
        content: none!important;
      }      
      padding-right: 0;
    }
  }
  .offer-actions {
    .icon {
      position: relative;
      z-index: 1;
      // transition: none;

      &:before {
        position: relative;
        z-index: 2;
      }

      &:after {
        content: "";
        position: absolute;
        width: 22px;
        height: 22px;
        top: 0;
        left: 0;
        margin: -3px 0 0 -4px;
        z-index: 1;
        border-radius: 100%;
        // transform: scale(.1) translateZ(0);
        opacity: 0;
        transition: 0.2s all ease-out;
      }

      > span {
        display: block;
        position: absolute;
        // min-width: 54px;
        height: 0;
        margin: -36px auto 0;
        padding: 7px 0;
        top: 0;
        left: -10px;
        right: -10px;
        color: $paletteDefaultAccent;
        background: $paletteDefaultText;
        border-radius: 2px;
        z-index: 3;
        text-align: center;
        // line-height: 1.5;
        visibility: hidden;
        opacity: 0;
        transform: translateY(-7px);
        font-size: em(14px);
        transition: 0.1s ease-out;
        // transform-origin: 50% 100%;

        &:before {
          content: "";
          position: absolute;
          margin: 0 auto;
          z-index: 1;
          left: 0; right: 0;
          bottom: -7px;
          width: 4px;
          height: 4px;
          border-width: 4px;
          border-style: solid;
          border-right-color: transparent;
          border-left-color: transparent;
          border-bottom-color: transparent;
          border-top-color: $paletteDefaultText;
        }
      }

      &.action-views,
      &.action-dislike {
        color: desaturate(lighten($primaryColor, 55%), 30%);

        &:after {
          background-color: desaturate(lighten($primaryColor, 40%), 30%);
        }
      }

      &.action-views {
        padding-top: 1px;
      }

      &.action-dislike {
        padding-top: 2px;

        &.is-dislike {
          color: $palettePrimaryText;

          &:after {
            opacity: .9;
            animation: pulseBox 2s normal forwards ease-out;
          }
        }
      }

      &.action-like {
        color: lighten($primaryColor, 50%);

        &:after {
          background-color: saturate(lighten($primaryColor, 40%), 40%);
        }

        &.is-like {
          color: $palettePrimaryText;

          &:after {
            opacity: .9;
            animation: pulseBox 2s normal forwards ease-out;
          }
        }
      }
      @media (min-width: 998px) {
        &:hover {

          > span {
            height: auto;
            opacity: 1;
            visibility: visible;
            transform: scale(1) translate(0, 0);
          }

          &:before {
            color: #FFF;
          }

          &:after {
            opacity: .7;
            // transform: scale(1) translateZ(0);
          }
        }
      }
    }
  }
  
  .cart-actions-cover {
    opacity: 0;
    transform: scale(.1) skewX(15deg) rotateY(-30deg);
    transition: 0.2s ease-out;
    .bg-dark{
      border-radius: 10px 10px 0 0;
    }
    div.xxs-mb-10 {
      transform: translateY(-5px);
      opacity: 0;
      transition: .2s ease-out;
      transition-delay: .2s;

      &:first-child {
        transition-delay: .3s;
      }
    }

    .details-btn {

      .arrow {
        transition: 0.1s ease-out;
      }

      &:hover .arrow {
        transform: translateX(-5px);
      }
    }
  }
  @media (min-width: 998px) {
    &:hover .cart-actions-cover {
      opacity: 1;
      transform: scale(1);

      div.xxs-mb-10 {
        transform: translateY(0);
        opacity: 1;
      }
    }
  }
  @media (max-width: 998px) {
    .cart-actions-cover {
      display: none;
    }
  }
  
}


// ===============
// New Main Slider
// ===============
.carousel{

  &.mainslider{
    border-radius: 10px;    
    .slide-item{
      border-radius: 10px;      
      img{
        border-radius: 10px;
      }
    }
    .owl-dots{
      position: absolute;
      text-align: center;
      transition: .3s;
      transform: translate3d(0,0,0);
      z-index: 10;
      bottom: 20px;
      right: 0;
      width: 100%;
      .owl-dot{
        position: relative;
        z-index: 12;
        display: inline-block;
        padding: 0 2px;
        cursor: pointer;
        span {
          display: block; 
          border-radius: 50%;
          border: 2px solid #fff;
          width: 15px;
          height: 15px;
          border-radius: 100%;
          background: transparent;
          box-shadow: 0 1px 2px #333;
        }
        &.active{
          span{
            background-color: #fff;
          }          
        }
      }
    }
  }
  
  .main-arrows{
    span {
      padding: 25px 12px;
      margin-top: -33px;
      color: #fff;
      background-color: transparent;
      border-radius: none;
      &:before{
        font-size: em(38px);
        text-shadow: 0 1px 3px rgba(0, 0, 0, 0.45);
      }
    }
  
    &.arrow-text span {
      margin-top: -80px;
      padding: 25px 0;
      line-height: 2;
      font-size: em(20px);
      font-weight: bold;
      color: $defaultTextColor;
      background-color: transparent;
  
      .icon {
        display: inline-block;
        vertical-align: text-top;
        padding-top: 10px;
        font-size: em(10px, 14px);
      }
    }    
      
  }
  
  @media (max-width: 998px) {
    .main-arrows{
      display: none;
    }
  }

}  

.offers-panner{
  margin-top: 60px;
  img{
    display: table;
    margin: auto;
    border-radius: 15px;
    // box-shadow: 0 1px 15px rgba(0, 0, 0, 0.3);
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.12), 0 3px 8px rgba(0, 0, 0, 0.08);
  }
}

#reservations-slider{
  .card-wrapper{
    max-width: 225px;
    margin-right: 4px;
    margin-left: 16px;
    min-height: 175px;
    max-height: 270px;    
  }
  .carousel-arrow span{
    padding: 12px;
    margin-top: 0;
    border-radius: 100%;
  }
}

.section-accent{
  background: linear-gradient(90deg, #840053 0%, #ba3787 100%);
  padding: 50px 0 60px;
  &.section-stars{
    position: relative;
    &:before{
      content: "";
      position:  absolute;
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      background: url("/frontend/images/starfield.png");
      background-position: center;    
    }
  }
  .section-title{
    border-bottom-color: rgba($border-color, 0.2); 
    .text{
      color: #fff;
      &:before{
        border-bottom-color: rgba($border-color, 0.4);
      }
    }
    .more-btn{
      color: #fff;
    }

  }
}

.std-nav .nav-bar{
  background: linear-gradient(90deg, #840053 0%, #ba3787 100%);
  position: relative;
  @extend %stars;
  &:before{
    opacity: 0.5;
  }
}

footer{
  position: relative;
  @extend %stars;  
  &:before{
    opacity: 0.2;
  }
}

%stars{
  &:before{
    content: "";
    position:  absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    background: url("/frontend/images/starfield.png");
    background-position: center;
  }  
}
.section-gray{
  background-color: #ddd;
  margin-bottom: -40px;
  padding: 60px 0;
}
.section-title-center{
  .text{
    text-align: center;  
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 30px;
  }
}
.section-sub-title-center{
  text-align: center;  
  font-size: 14px;
  font-weight: 600;
}
.stores-buttons{
  text-align: center;
  .btn{
    position: relative;
    width: 140px;
    height: 50px;
    padding: 0;
    margin-top: 15px;
    border-radius: 8px;
    display: inline-block;
    background-color: #000;
    color: #fff;
    span{
      display: block;
      line-height: 50px;
    }
    .btn-img{
      position: absolute;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 8px;
      background-color: transparent;      
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;    }
