      margin-right: 4px!important;
      margin-left: 4px!important;
      &+.btn{
        margin-top: 6px;
      }           
    }
  }
}

.offers-live{
  .section-title{
    padding: 10px 15px 0 10px;
  }
  .btn-link{
    font-family:  $font-jfflat;
  }
  .offers-box{
    // border-radius: 15px;
  }
  .offer-card .card-img .card-cover{
    top: 0;
    right: 0;
    border-radius: 15px;
  }  
  @media (max-width: 485px){
    &.boxed-offers .offer-card{
      width: 100%!important;
    }
  }
}

#featured-tab{
  .nav-tabs{
    border: none;
    padding-right: 0;
    li{
      float: right;
      a{
        font-family:  $font-jfflat;    
        margin-right: 6px;
        border: none!important;
        border-radius: 2px!important;
        color: #fff;
        padding: 6px;
        background-color: #aaa9a9;
      }
      &.active{
        a{
          background-color: #8f0f5f;
        }
      }
      &:first-child{
        a{
          margin-right: 0;
        }
      }
    }
  }
  .tab-content{
    position: relative;
    .owl-prev{
      .carousel-arrow{
        right: -80px;
      }
    }
    .owl-next{
      .carousel-arrow{
        left: -80px;
      }
    }
    .slide{
      overflow: hidden;
      min-height: 255px;
      .carousel-container{
        // width: 1600px;
        padding-bottom: 15px;
        transition: transform 0.4s ease;
        .card-wrapper{
          margin-right: 5px;
          margin-left: 25px;
          width: 180px;
          display: inline-block;
          &:last-child{
            margin-left: 0;
          }
        }
      }
    }
    @media (max-width: 998px) {
      .carousel-arrows{
        display: none;
      }
      .slide{
        overflow-x: auto;
        .carousel-container{
          transform: translateX(0)!important;
        }
      }
    }    
  }
}
